

.main-content strong {
  font-weight: 900
}

.landing7 {
  scroll-snap-align: start;
}

/* sm 이하 화면 크기에서 scrollSnapAlign을 제거 */
@media (max-width: 639px) {
  .landing7 {
    scroll-snap-align: unset;
  }
}

@media (max-width: 300px) {
  .mobile-hide {
    display: none;
  }
  .mobile-scale-down{
    transform: scale(0.8);
  }
  .mobile-scale-down-2{
    transform: scale(0.5);
  }
}

@media (min-width: 1920px) {
  .qhd-scale-up {
    transform: scale(1.4);
  }
}